// styling
import styles from './style.module.scss';

// components
import AuthLayout from '@components/AuthLayout';
import {NavLink} from 'react-router-dom';

// hooks
import {useThemeProvider} from '@contexts/themeContext';

// assets
import logo from '@assets/logo-dark.svg';
import facebook from '@assets/icons/facebook.svg';
import google from '@assets/icons/google.svg';
import appleDark from '@assets/icons/apple.svg';
import appleLight from '@assets/icons/apple-dark.svg';

const SignIn = () => {
    const {theme} = useThemeProvider();

    return (
        <AuthLayout title="Sign In">
            <div className="flex-1">
                <div className="d-flex flex-column g-32">
                    <div className={`${styles.header} d-flex flex-column align-items-center g-16`}>
                        <img className={styles.header_logo} src={logo} alt="InCrypto" />
                        <h1>Start Your Crypto Investment Today</h1>
                    </div>
                    <div className="d-flex flex-column g-16">
                        <button className={`${styles.button} card ${theme === 'dark' ? 'card--no-bg' : ''} bordered bordered--hover`}>
                            <img src={facebook} alt="Facebook" />
                            Continue with Facebook
                        </button>
                        <button className={`${styles.button} card ${theme === 'dark' ? 'card--no-bg' : ''} bordered bordered--hover`}>
                            <img src={theme === 'dark' ? appleDark : appleLight} alt="Apple" />
                            Continue with Apple
                        </button>
                        <button className={`${styles.button} card ${theme === 'dark' ? 'card--no-bg' : ''} bordered bordered--hover`}>
                            <img src={google} alt="Google" />
                            Continue with Google
                        </button>
                        <button className={`btn ${theme === 'dark' ? 'btn--hover-grey' : ''}`}>
                            Sing Up with phone or email
                        </button>
                    </div>
                </div>
                <div className="auth-footer">
                    <p className="label">Already have an account?</p>
                    <NavLink className={`btn ${theme === 'dark' ? 'btn--grey' : 'btn--invert'}`} to="/sign-up">
                        Sing Up
                    </NavLink>
                </div>
            </div>
        </AuthLayout>
    )
}

export default SignIn